//Base URL
const base_url = "https://api.rawg.io/api/";
const API_KEY_URL = `?&key=${process.env.REACT_APP_API}`;
// Getting the month
const getCurrentMonth = () => {
    const month = new Date().getMonth() + 1;
    if (month < 10) {
        return `0${month}`;
    }
    else {
        return month;
    }
};

// Getting the date
const getCurrentDay = () => {
    const day = new Date().getDate() + 1;
    if (day < 10) {
        return `0${day}`;
    }
    else {
        return day;
    }
};

// Current day/month/year
const currentYear = new Date().getFullYear();
const currentMonth = getCurrentMonth();
const currentDay = getCurrentDay();
const currentDate = `${currentYear}-${currentMonth}-${currentDay}`;
const lastYear = `${currentYear - 1}-${currentMonth}-${currentDay}`;
const nextYear = `${currentYear + 1}-${currentMonth}-${currentDay}`;

// Popular Games
const popular_games = `games${API_KEY_URL}&dates=${lastYear},${currentDate}&ordering=-rating&page_size=10`;
const upcoming_games = `games${API_KEY_URL}&dates=${currentDate},${nextYear}&ordering=-added&page_size=10`;
const new_games = `games${API_KEY_URL}&dates=${lastYear},${currentDate}&ordering=-released&page_size=10`;

export const popularGamesURL = () => `${base_url}${popular_games}${API_KEY_URL}`;
export const upcomingGamesURL = () => `${base_url}${upcoming_games}${API_KEY_URL}`;
export const newGamesURL = () => `${base_url}${new_games}${API_KEY_URL}`;
//GAME DETAILS
export const gameDetailsURL = (game_id) => `${base_url}games/${game_id}${API_KEY_URL}`;
//Game ScreenShots
export const gameScreenshotURL = (game_id) => `${base_url}games/${game_id}/screenshots?&.json${API_KEY_URL}`;
//Searched game
export const searchGameURL = (game_name) => `${base_url}games?search=${game_name}&page_size=9${API_KEY_URL}`;